import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { clearCart } from "../redux/CartSlice";
import { clearCart1 } from "../redux/CartSlice1";
import { RiDeleteBack2Line } from "react-icons/ri";
import {
  FaShippingFast,
  FaCreditCard,
  FaPercent,
  FaComment,
  FaCheckCircle,
  FaClipboard,
  FaRegCopy,
} from "react-icons/fa";
import { MdMyLocation } from "react-icons/md";
import { LoadScript, Autocomplete } from "@react-google-maps/api";

import { AiFillCheckCircle } from "react-icons/ai";
import { toast, ToastContainer } from "react-toastify";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
const GOOGLE_API_KEY = process.env.REACT_APP_GOOGLE_API_KEY; // Replace with your Google Maps API key

const Checkout = () => {
  const dispatch = useDispatch();
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 2,
    autoplay: true,
    autoplaySpeed: 3000,
    responsive: [
      { breakpoint: 768, settings: { slidesToShow: 1, slidesToScroll: 1 } },
    ],
  };

  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;

  const [shipToDifferentAddress, setShipToDifferentAddress] = useState(false);
  const [selectedAddress, setSelectedAddress] = useState("");
  const [paymentMethod, setPaymentMethod] = useState("");
  const [addresses, setAddresses] = useState([]);
  const [newAddress, setNewAddress] = useState({
    fullName: "",
    street: "",
    city: "",
    state: "",
    country: "",
    pincode: "",
    number: "",
    anumber: "",
    email: "",
  });
  const [autocomplete, setAutocomplete] = useState(null);
  const handlePlaceSelect = () => {
    if (!autocomplete) return;

    const place = autocomplete.getPlace();
    if (!place.geometry || !place.address_components) return;

    const addressComponents = place.address_components;

    let street = place?.name;
    let city = "";
    let state = "";
    let country = "";
    let pincode = "";
    let lat = place.geometry.location.lat();
    let lng = place.geometry.location.lng();

    // Extract address components
    addressComponents.forEach((component) => {
      const types = component.types;

      if (types.includes("locality")) {
        city = component.long_name;
      }
      if (types.includes("administrative_area_level_1")) {
        state = component.short_name;
      }
      if (types.includes("country")) {
        country = component.long_name;
      }
      if (types.includes("postal_code")) {
        pincode = component.long_name;
      }
    });

    // Update state with extracted data
    setNewAddress({
      ...newAddress,
      street,
      city,
      state,
      country,
      pincode,
      lat,
      lng,
    });
  };

  const getAddressFromCoordinates = (lat, lng) => {
    fetch(
      `https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${lng}&key=${GOOGLE_API_KEY}`
    )
      .then((response) => response.json())
      .then((data) => {
        if (data.status === "OK" && data.results.length > 0) {
          const place = data.results[0];
          const components = place.address_components;

          let street = place?.formatted_address || "";
          let city = "";
          let state = "";
          let country = "";
          let pincode = "";

          components.forEach((component) => {
            const types = component.types;
            if (types.includes("locality")) city = component.long_name;
            if (types.includes("administrative_area_level_1"))
              state = component.long_name;
            if (types.includes("country")) country = component.long_name;
            if (types.includes("postal_code")) pincode = component.long_name;
          });

          setNewAddress({
            ...newAddress,
            street,
            city,
            state,
            country,
            pincode,
            lat,
            lng,
          });
        } else {
          toast.info("Unable to fetch address details.");
        }
      })
      .catch(() => toast.info("Failed to fetch address"));
  };

  const getLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          getAddressFromCoordinates(latitude, longitude);
        },
        (err) => toast.info(err.message)
      );
    } else {
      toast.info("Geolocation is not supported by this browser.");
    }
  };

  const [coupon, setCoupon] = useState("");
  const [couponDiscount, setCouponDiscount] = useState(0);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const [additionalInstructions, setAdditionalInstructions] = useState("");
  const encryptedUser = Cookies.get("user");
  const user = encryptedUser
    ? JSON.parse(
        CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;

  const items = useSelector((state) => state.cart.items);
  const items1 = useSelector((state) => state.cart1.items1);
  const subtotal = useSelector((state) => state.cart.subtotal);
  const location = useLocation();
  const { orderType, deliveryCharge, minKm, perkmprice, minAmount } =
    location.state || {};

  const total = useSelector((state) => state.cart.total);
  const subtotal1 = useSelector((state) => state.cart1.subtotal1);
  const total1 = useSelector((state) => state.cart1.total1);
  const [AllCupons, setAllCoupons] = useState([]);
  const getAllCupons = async () => {
    try {
      let res = await axios.get(
        `https://majesticmall.net/api/admin/getavailablecoupons`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setAllCoupons(res.data.availableCoupons);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const openship = () => {
    setShipToDifferentAddress(!shipToDifferentAddress);
    setSelectedAddress("");
  };

  const getAddresses = async () => {
    try {
      const res = await axios.get(
        `https://majesticmall.net/api/user/address/${user._id}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setAddresses(res.data.success);
      }
    } catch (error) {
      console.error("Error fetching addresses:", error);
    }
  };

  const handleAddressChange = (e) => {
    setSelectedAddress(JSON.parse(e.target.value));
    setShipToDifferentAddress(false);
    // calculateDistance(
    //   `${
    //     selectedAddress.address1
    //       ? selectedAddress.address1
    //       : selectedAddress.street
    //   } ${
    //     selectedAddress.address2
    //       ? selectedAddress.address2
    //       : selectedAddress.city
    //   } ${selectedAddress.state} ${selectedAddress.pincode}`
    // );
  };

  const handleNewAddressChange = (e) => {
    const { name, value } = e.target;
    setNewAddress({ ...newAddress, [name]: value });
    if (name === "fullName") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid name: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid name: Minimum 3 characters");
        return;
      }
    }
    if (name === "city") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid city: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid city: Minimum 3 characters");
        return;
      }
    }
    if (name === "street" && value.length < 3) {
      // console.log("Invalid street: Minimum 3 characters");
      return;
    }
    if (name === "state") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid state: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid state: Minimum 3 characters");
        return;
      }
    }
    if (name === "country") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid country: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid country: Minimum 3 characters");
        return;
      }
    }
    if (name === "pincode" && (!/^\d*$/.test(value) || value.length > 6)) {
      // console.log("Invalid pincode: Must be a number with max 6 digits");
      return;
    }
    if (name === "number" && (!/^\d*$/.test(value) || value.length > 10)) {
      // console.log("Invalid mobile: Must be a number with max 10 digits");
      return;
    }
    if (name === "anumber" && (!/^\d*$/.test(value) || value.length > 10)) {
      // console.log("Invalid mobile: Must be a number with max 10 digits");
      return;
    }
  };

  const addAddress = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        "https://majesticmall.net/api/user/addaddress",
        {
          userId: user._id,
          ...newAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        toast.info("Address added successfully");
        getAddresses();
        setNewAddress({
          fullName: "",
          street: "",
          city: "",
          state: "",
          country: "",
          pincode: "",
          number: "",
          anumber: "",
          email: "",
        });
      }
    } catch (error) {
      toast.info(error.response?.data?.error || "Error adding address");
    }
  };

  const deleteAddress = async (id) => {
    try {
      const res = await axios.delete(
        `https://majesticmall.net/api/user/deleteaddress/${id}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        toast.info("Address deleted successfully");
        getAddresses();
      }
    } catch (error) {
      console.error("Error deleting address:", error);
    }
  };

  const applyCoupon = async () => {
    try {
      const res = await axios.post(
        "https://majesticmall.net/api/admin/checkpromocode",
        {
          promocode: coupon,
          userId: user._id,
          userName: user?.name,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setCouponDiscount(res.data.discount);
        toast.success("Successfully Coupon applied");
      }
    } catch (error) {
      toast.info(error.response.data.error);
    }
  };

  const totalDiscount = (orderType !== "scheduled" ? items : items1).reduce(
    (total, item) => {
      // Calculate the discount for the current item
      const discount =
        (item.selectedWeight?.price * item?.quantity || 0) *
        ((item.selectedWeight?.discount ?? item?.discount ?? 0) / 100);

      return total + discount; // Accumulate the total discount
    },
    0 // Initial total discount is 0
  );

  //   function calculatDisAmount(
  //     minKm,
  //     perKmPrice,
  //     minAmount,
  //     distance,
  //     totalAmount
  //   ) {
  //     // Condition 1: If minAmount is less than totalAmount and minKm is less than distance
  //     if (!distance) return 0;

  //     // distance = parseFloat(distance.match(/[\d.]+/)[0]);
  //      // Extract numeric value from the distance string
  //      let numericDistance = parseFloat(distance.match(/[\d.]+/)[0]);

  //      // Determine unit (meters or kilometers)
  //      let unit = distance.includes('km') ? 'km' : 'm';

  //      // Convert meters to kilometers if necessary
  //      if (unit === 'm') {
  //          numericDistance /= 1000; // Convert meters to kilometers
  //      }

  //     if (minAmount < totalAmount && minKm > numericDistance) {
  //       return 0;
  //     }

  //     // Condition 2: If distance is greater than minKm, return distance * perKmPrice
  //     if (numericDistance > minKm) {
  //       return distance * perKmPrice;
  //     }
  // console.log("numericDistance",numericDistance)
  //     // Default: Return perKmPrice * distance
  //     return perKmPrice * numericDistance;
  //   }
  function calculatDisAmount(
    minKm,
    perKmPrice,
    minAmount,
    distance,
    totalAmount
  ) {
    // Check if distance is provided
    if (!distance) return 0;

    // Extract numeric value from the distance string
    let numericDistance = parseFloat(distance.match(/[\d.]+/)[0]);

    // Determine unit (meters or kilometers)
    let unit = distance.includes("km") ? "km" : "m";

    // Convert meters to kilometers if necessary
    if (unit === "m") {
      numericDistance /= 1000; // Convert meters to kilometers
    }

    // Apply conditions based on minAmount, minKm, and distance
    if (minAmount < totalAmount && minKm > numericDistance) {
      return 0; // Discount amount is 0
    }

    // Calculate discount based on perKmPrice and numericDistance
    if (numericDistance > minKm) {
      return numericDistance * perKmPrice; // Distance greater than minKm
    }
    // console.log("numericDistance",numericDistance)
    // Default case: Calculate discount based on perKmPrice and numericDistance
    return perKmPrice * numericDistance;
  }

  const placeOrder = async () => {
    if (!selectedAddress) {
      toast.info("Please select a shipping address");
      return;
    }
    if (!paymentMethod) {
      toast.info("Please select a payment method");
      return;
    }

    const orderData = {
      userId: user._id,

      items:
        orderType !== "scheduled"
          ? items.map((item) => ({
              productId: item._id,
              name: item.productname,
              quantity: item.quantity,
              price:
                item.selectedWeight?.price -
                item.selectedWeight?.price *
                  ((item.selectedWeight?.discount
                    ? item.selectedWeight?.discount
                    : item?.discount) /
                    100),
              discount:
                item.selectedWeight?.price *
                ((item.selectedWeight?.discount
                  ? item.selectedWeight?.discount
                  : item?.discount) /
                  100),
              selecteddiscount: item.selectedWeight?.discount
                ? item.selectedWeight?.discount
                : item?.discount,
              weight: `${item.selectedWeight.value}${item.selectedWeight.units}`,
            }))
          : items1.map((item) => ({
              productId: item._id,
              name: item.productname,
              quantity: item.quantity,
              price:
                item.selectedWeight?.price -
                item.selectedWeight?.price *
                  ((item.selectedWeight?.discount
                    ? item.selectedWeight?.discount
                    : item?.discount) /
                    100),
              discount:
                item.selectedWeight?.price *
                ((item.selectedWeight?.discount
                  ? item.selectedWeight?.discount
                  : item?.discount) /
                  100),
              selecteddiscount: item.selectedWeight?.discount
                ? item.selectedWeight?.discount
                : item?.discount,
              weight: `${item.selectedWeight.value}${item.selectedWeight.units}`,
            })),
      totalAmount: (
        (orderType !== "scheduled" ? total : total1) +
        deliveryCharge +
        calculatDisAmount(
          minKm,
          perkmprice,
          minAmount,
          distance,
          orderType !== "scheduled" ? total : total1
        ) -
        (orderType !== "scheduled" ? total : total1) * (couponDiscount / 100)
      ).toFixed(2),
      discount:
        ((orderType !== "scheduled" ? subtotal : subtotal1) + totalDiscount) *
          (couponDiscount / 100) +
        totalDiscount,
      shippingAddress: selectedAddress,
      paymentMethod: paymentMethod,
      additionalInstructions: additionalInstructions,
      orderType: orderType,
      deliveryCharge:
        deliveryCharge +
        calculatDisAmount(
          minKm,
          perkmprice,
          minAmount,
          distance,
          orderType !== "scheduled" ? total : total1
        ),
    };
    const config = {
      url: "/user/createOrder",
      method: "post",
      baseURL: "https://majesticmall.net/api",
      hearder: { "content-type": "application/json" },
      headers: {
        Authorization: `Bearer ${token}`,
      },
      data: orderData,
    };

    try {
      const res = await axios.post(
        paymentMethod === "Online Payment"
          ? "https://majesticmall.net/api/user/addPaymentPhone"
          : "https://majesticmall.net/api/user/createOrder",
        {
          ...orderData,
          username: user?.name,
          Mobile: user?.mobile,
          orderId: orderData?.items[0]?.productId,
          amount: orderData.totalAmount,
          config: JSON.stringify(config),
          orderType: orderType,
          redirecturl: "/account",
          callBack: "/cart",
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        if (paymentMethod === "Online Payment") {
          return window.location.assign(res.data?.url?.url);
        }
        toast.success("Order placed successfully!");
        if (orderType !== "scheduled") {
          dispatch(clearCart());
        } else {
          dispatch(clearCart1());
        }

        setTimeout(() => {
          window.location.assign("/");
        }, 2000);
      }
    } catch (error) {
      console.error("Error placing order:", error);
      toast.info("An error occurred while placing the order.");
    }
  };
  const [coponcode, setcouponcode] = useState("");
  const copyToClipboard = (code) => {
    navigator.clipboard.writeText(code);
    setcouponcode(code);
    toast.success(`Coupon code "${code}" copied to clipboard!`);
  };

  const [distance, setDistance] = useState(0);

  useEffect(() => {
    const fetchDistance = async () => {
      if (!selectedAddress) return;
      if (!selectedAddress?.lat || !selectedAddress.lng)
        return toast.error("Your address not able to find please update");

      if (!window.google) return;

      const origin = { lat: 12.940025819400102, lng: 77.5477817106305 };
      const destination = {
        lat: selectedAddress?.lat,
        lng: selectedAddress.lng,
      };

      const service = new window.google.maps.DistanceMatrixService();

      service.getDistanceMatrix(
        {
          origins: [origin],
          destinations: [destination],
          travelMode: window.google.maps.TravelMode.DRIVING,
        },
        (response, status) => {
          if (status === "OK") {
            const result = response.rows[0].elements[0];
            setDistance(result.distance.text);
          } else {
            console.error("Distance Matrix request failed:", status);
          }
        }
      );
    };

    fetchDistance();
  }, [selectedAddress]);

  console.log("distance====>", distance, selectedAddress);

  useEffect(() => {
    getAllCupons();
    getAddresses();
    if (user && !selectedAddress) {
      setSelectedAddress(user);
    }
  }, []);

  // useEffect(() => {

  //   if (selectedAddress) {
  //     const address = `${selectedAddress.address1 || selectedAddress.street} ${
  //       selectedAddress.address2 || selectedAddress.city
  //     } ${selectedAddress.state} `;

  //     calculateDistance(address);
  //   }
  // }, [selectedAddress]);

  return (
    <div className="container px-4 py-8 mx-auto bg-gray-50">
      <ToastContainer />
      {/* <h1 className="mb-8 text-4xl font-bold text-center text-gray-800">
        Checkout
      </h1> */}

      <h1 className="text-2xl md:text-4xl lg:text-4xl font-bold text-center quicksand mt-1 text-[#f2281c] mb-3">
        Checkout
      </h1>

      <div className="grid grid-cols-1 gap-12 md:grid-cols-2">
        <div className="p-6 bg-white rounded-lg shadow-md">
          <h2 className="flex items-center mb-6 text-xl font-semibold">
            <FaShippingFast className="mr-2 text-[#000]" />
            Shipping Address
          </h2>
          <select
            className="w-full p-2 border border-gray-300 rounded-md mb-4 focus:outline-none focus:ring-2 focus:ring-[#000]"
            value={JSON.stringify(selectedAddress)}
            onChange={handleAddressChange}
          >
            <option value="{}">Select an address</option>

            <option key={user._id} value={JSON.stringify(user)}>
              {user.name}, {user.address1}, {user.address2}
            </option>
            {addresses?.map((address) => (
              <option key={address._id} value={JSON?.stringify(address)}>
                {address.fullName}, {address.street}, {address.city}
              </option>
            ))}
          </select>

          {selectedAddress && (
            <div className="p-4 mb-4 bg-gray-100 rounded-md">
              <h3 className="mb-2 font-semibold text-gray-700">
                Selected Address
              </h3>
              <p>
                {selectedAddress.name
                  ? selectedAddress.name
                  : selectedAddress.fullName}
              </p>
              <p>
                {selectedAddress.address1
                  ? selectedAddress.address1
                  : selectedAddress.street}
              </p>
              <p>
                {selectedAddress.address2
                  ? selectedAddress.address2
                  : selectedAddress.city}
                , {selectedAddress.state} {selectedAddress.pincode}
              </p>
              <p>{selectedAddress.country}</p>
              <p>
                Phone:{" "}
                {selectedAddress.mobile
                  ? selectedAddress.mobile
                  : selectedAddress.number}
              </p>
              <p>
                Alternative Phone(Whatsapp number):{" "}
                {selectedAddress.Amobile
                  ? selectedAddress.Amobile
                  : selectedAddress.anumber}
              </p>
              <p>Email: {selectedAddress.email}</p>
            </div>
          )}

          <div className="mb-4">
            <label className="flex items-center text-gray-700">
              <input
                type="checkbox"
                checked={shipToDifferentAddress}
                onChange={() => openship()}
                className="mr-2 form-checkbox h-5 w-5 text-[#000]"
              />
              Ship to a different address?
            </label>
          </div>

          {shipToDifferentAddress && (
            <form onSubmit={addAddress} className="space-y-4">
              <input
                type="text"
                name="fullName"
                value={newAddress.fullName}
                onChange={handleNewAddressChange}
                placeholder="Full Name"
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                required
              />
              <div className="relative flex items-center rounded-full align-center">
                <MdMyLocation
                  className="absolute ml-2 cursor-pointer left-3"
                  color="#000"
                  title="Current Location"
                  onClick={() => getLocation()}
                />
                <Autocomplete
                  onLoad={setAutocomplete}
                  onPlaceChanged={handlePlaceSelect}
                  className="px-2 w-100 "
                >
                  <input
                    type="text"
                    name="street"
                    value={newAddress.street}
                    onChange={handleNewAddressChange}
                    placeholder="Street Address"
                    className="w-full    border border-gray-300  rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                    style={{ paddingLeft: "40px" }}
                  />
                </Autocomplete>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <input
                  type="text"
                  name="city"
                  value={newAddress.city}
                  onChange={handleNewAddressChange}
                  placeholder="City"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
                <input
                  type="text"
                  name="state"
                  value={newAddress.state}
                  onChange={handleNewAddressChange}
                  placeholder="State"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <input
                  type="text"
                  name="country"
                  value={newAddress.country}
                  onChange={handleNewAddressChange}
                  placeholder="Country"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
                <input
                  type="text"
                  name="pincode"
                  value={newAddress.pincode}
                  onChange={handleNewAddressChange}
                  placeholder="Pincode"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <input
                  type="tel"
                  name="number"
                  value={newAddress.number}
                  onChange={handleNewAddressChange}
                  placeholder="Phone Number"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
                <input
                  type="tel"
                  name="anumber"
                  value={newAddress.anumber}
                  onChange={handleNewAddressChange}
                  placeholder="Alternative Number (Whatsapp number)"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
              </div>
              <input
                type="email"
                name="email"
                value={newAddress.email}
                onChange={handleNewAddressChange}
                placeholder="Email"
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                required
              />
              <button
                type="submit"
                className="w-full bg-[#000] text-white p-2 rounded-md hover:bg-[#000] transition duration-300  hover:opacity-80"
              >
                Add New Address
              </button>
            </form>
          )}
          {AllCupons?.length > 0 && (
            <div className="p-4 mx-auto ">
              <h1 className="mb-4 text-2xl font-bold text-center">
                Available Coupons
              </h1>

              <ul className="pl-5 space-y-2 overflow-y-auto list-disc max-h-80">
                {AllCupons?.map((coupon, i) => (
                  <li key={i} className="text-gray-800">
                    <div className="flex items-center justify-between">
                      <span className="font-semibold">{coupon?.code}</span>
                      <span className="px-3 py-1 text-sm font-bold text-white bg-green-500 rounded">
                        {coupon?.discountpercentage}% OFF
                      </span>
                      <button
                        onClick={() => copyToClipboard(coupon.code)}
                        disabled={coponcode === coupon.code}
                        className={`mt-2 w-10 h-10 flex items-center justify-center rounded-full 
                          ${
                            coponcode === coupon.code
                              ? "bg-green-500 hover:bg-green-600"
                              : "bg-red-500 hover:bg-red-600"
                          }
                           text-white border-2 border-white shadow-lg`}
                      >
                        {coponcode === coupon.code ? (
                          <FaCheckCircle
                            className="w-5 h-5 text-white"
                            title="Copied"
                          />
                        ) : (
                          <FaRegCopy
                            className="w-5 h-5 text-white"
                            title="Copy Code"
                          />
                        )}
                      </button>
                    </div>
                    <p className="text-sm text-gray-600">{coupon?.promocode}</p>

                    <hr />
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>

        <div className="p-6 bg-white rounded-lg shadow-md">
          <h2 className="mb-6 text-xl font-semibold">Order Summary</h2>
          <div className="p-4 bg-gray-100 rounded-md">
            {orderType !== "scheduled"
              ? items.map((item) => (
                  <div
                    key={item._id}
                    className="flex items-center justify-between mb-2 text-gray-700"
                  >
                    <span>
                      {item.productname} x {item.quantity}
                    </span>
                    <span>
                      
                      {(
                        (item.selectedWeight?.price -
                          item.selectedWeight?.price *
                            ((item.selectedWeight?.discount
                              ? item.selectedWeight?.discount
                              : item.discount) /
                              100)) *
                        item.quantity
                      ).toFixed(2)}
                    </span>
                  </div>
                ))
              : items1.map((item) => (
                  <div
                    key={item._id}
                    className="flex items-center justify-between mb-2 text-gray-700"
                  >
                    <span>
                      {item.productname} x {item.quantity}
                    </span>
                    <span>
                      
                      {(
                        (item.selectedWeight?.price -
                          item.selectedWeight?.price *
                            ((item.selectedWeight?.discount
                              ? item.selectedWeight?.discount
                              : item.discount) /
                              100)) *
                        item.quantity
                      ).toFixed(2)}
                    </span>
                  </div>
                ))}
            <div className="pt-4 mt-4 border-t border-gray-300">
              <div className="flex justify-between mb-2 text-gray-700">
                <span>Total MRP</span>
                <span>
                  
                  {(
                    (orderType !== "scheduled" ? subtotal : subtotal1) +
                    totalDiscount
                  ).toFixed(2)}
                </span>
              </div>

              <div className="flex justify-between mb-2 text-gray-700">
                <span>Shipping</span>
                <span>
                  
                  {/* {(orderType !== "scheduled" ? total : total1) >= 1000
                    ? 0
                    : 40} */}
                  {(
                    deliveryCharge +
                    calculatDisAmount(
                      minKm,
                      perkmprice,
                      minAmount,
                      distance,
                      orderType !== "scheduled" ? total : total1
                    )
                  )?.toFixed(2)}
                </span>
              </div>

              <div className="flex justify-between mb-4 text-gray-700">
                <span>Discount</span>
                <span>-{totalDiscount?.toFixed(2)}</span>
              </div>
              {couponDiscount > 0 && (
                <div className="flex justify-between text-[#000]">
                  <span>Coupon Discount</span>
                  <span>
                    -
                    {(
                      ((orderType !== "scheduled" ? subtotal : subtotal1) +
                        totalDiscount) *
                      (couponDiscount / 100)
                    ).toFixed(2)}
                  </span>
                </div>
              )}
              <hr />
              <div className="flex justify-between mt-4 text-lg font-bold">
                <span>Total</span>
                <span>
                  
                  {(
                    (orderType !== "scheduled" ? total : total1) +
                    deliveryCharge +
                    calculatDisAmount(
                      minKm,
                      perkmprice,
                      minAmount,
                      distance,
                      orderType !== "scheduled" ? total : total1
                    ) -
                    (orderType !== "scheduled" ? total : total1) *
                      (couponDiscount / 100)
                  ).toFixed(2)}
                </span>
              </div>
            </div>
          </div>

          <div className="mt-6">
            <div className="flex items-center mb-2">
              <FaPercent className="mr-2 text-[#000]" />
              <h3 className="mt-1 font-semibold text-gray-700">Apply Coupon</h3>
            </div>
            <div className="flex flex-grow  border border-gray-300 rounded-l-md focus:outline-none focus:ring-2 focus:ring-[#000] rounded-r-md">
              <input
                type="text"
                value={coupon}
                onChange={(e) => setCoupon(e.target.value)}
                placeholder="Enter coupon code"
                // className="flex-grow p-2 border border-gray-300 rounded-l-md focus:outline-none focus:ring-2 focus:ring-[#000]"
              />
              <button
                onClick={applyCoupon}
                className="bg-[#000] text-white p-2 rounded-r-md hover:bg-[#000] transition duration-300  hover:opacity-80"
              >
                Apply
              </button>
            </div>
          </div>
          <div className="mt-6">
            <div className="flex items-center mb-2">
              <FaComment className="mr-2 text-[#000]" />
              <h3 className="mt-2 font-semibold text-gray-700">
                Additional Instructions
              </h3>
            </div>
            <textarea
              value={additionalInstructions}
              onChange={(e) => setAdditionalInstructions(e.target.value)}
              placeholder="Enter any special instructions or notes for your order"
              className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000] h-24"
            />
          </div>
          <div className="mt-6">
            <div className="flex items-center mb-2">
              <FaCreditCard className="mr-2 text-[#000]" />
              <h3 className="mt-2 font-semibold text-gray-700">
                Payment Method
              </h3>
            </div>
            <div className="space-y-2">
              <label className="flex items-center p-2 transition duration-300 border border-gray-300 rounded-md cursor-pointer hover:bg-gray-50">
                <input
                  type="radio"
                  value="Cash On Delivery"
                  checked={paymentMethod === "Cash On Delivery"}
                  onChange={(e) => setPaymentMethod(e.target.value)}
                  className="mr-2 form-radio h-5 w-5 text-[#000]"
                />
                Cash On Delivery
              </label>
              <label className="flex items-center p-2 transition duration-300 border border-gray-300 rounded-md cursor-pointer hover:bg-gray-50">
                <input
                  type="radio"
                  value="Online Payment"
                  checked={paymentMethod === "Online Payment"}
                  onChange={(e) => setPaymentMethod(e.target.value)}
                  className="mr-2 form-radio h-5 w-5 text-[#000]"
                />
                Online Payment
              </label>
            </div>
          </div>

          <div className="flex items-center justify-center">
            <button
              onClick={placeOrder}
              className="bg-[#000] text-white p-2 rounded-md mt-6 hover:bg-[#000] transition duration-300 text-lg font-semibold  hover:opacity-80"
            >
              Place Order
            </button>
          </div>
        </div>
      </div>

      {isModalVisible && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
          <div className="w-full max-w-md p-4 bg-white rounded">
            <h2 className="mb-4 text-xl font-bold">Address List</h2>
            {addresses.map((address) => (
              <div
                key={address._id}
                className="flex items-center justify-between mb-2"
              >
                <span>
                  {address.fullName}, {address.city}
                </span>
                <button
                  onClick={() => deleteAddress(address._id)}
                  className="text-red-500"
                >
                  <RiDeleteBack2Line />
                </button>
              </div>
            ))}
            <button
              onClick={() => setIsModalVisible(false)}
              className="p-2 mt-4 text-black bg-gray-300 rounded hover:opacity-80"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Checkout;
